@media screen and (min-width: 1280px) {
  .switch {
    margin-left: auto;
    padding-top: 28px;
    padding-bottom: 34px;
    display: flex;
  }

  .switch input {
    width: 64px;
    height: 32px;
    cursor: pointer;
    -webkit-appearance: none;
    -ms-appearance: none;
    appearance: none;
    transition: all .5s;
    border: 2px solid #242424;
    border-radius: 34px;
    outline: none;
    -webkit-transition: all .4s;
    position: relative;
  }

  .switch input:checked {
    background-color: var(--secondary-accent-color);
    border: 2px solid #7e8fdd;
  }

  .switch input:before {
    content: "";
    width: 25px;
    height: 25px;
    background-color: var(--secondary-accent-color);
    border-radius: 100px;
    transition: all .5s;
    position: absolute;
    top: 2px;
    left: 2px;
    transform: scale(1.1);
  }

  .switch input:checked:before {
    background-color: #fff;
    left: 34px;
  }
}

